






import {Component, Prop, Vue} from "vue-property-decorator";

@Component
export default class Maj extends Vue {
  @Prop({default: 0, type: Number}) value: number
  @Prop({default: 30, type: Number}) size: number

  get style() {
    return {
      width: this.size + 'px',
      height: this.size + 'px',
      border: `${this.size / 20}px solid black`,
      fontSize: `${this.size / 1.5}px`
    }
  }
}
