







import {Component, Vue} from 'vue-property-decorator';
import Stage from "@/components/Stage.vue";
import $ from 'velocity-animate/velocity.js'
import 'velocity-animate/velocity.ui'
import Counter from "@/components/Counter.vue";
import Panel from "@/components/Panel.vue";

Vue.prototype.$animate = function (keys: Partial<CSSStyleDeclaration>, option?, el?: Element): Promise<void> {
  return new Promise(resolve => {
    const cb = option.complete;
    option.complete = () => {
      resolve();
      try {
        cb();
      } catch (e) {
      }
    }
    $(el || this.$el, keys, option)
  })
};
@Component({
  components: {Panel, Counter, Stage}
})
export default class App extends Vue {
  mounted() {
    (this.$root as any).delays = {
      move: 300,
      modal: {
        in: 200,
        delay: 500,
        out: 200
      }
    }
  }
}
