











import {Component, Prop, Vue} from "vue-property-decorator";
import Maj from "@/components/Maj.vue";

function sleep(t): Promise<void> {
  return new Promise(resolve =>
      setTimeout(() => resolve(), t))
}

const playerColors = ['#ffc0cb77', '#00cccc77']
@Component({
  components: {Maj}
})
export default class Modal extends Vue {
  title: string = 'Hello'
  PlayerType: 0 | 1 = 1

  get playerColor() {
    return playerColors[this.PlayerType]
  }

  majs: { maj: number, selected: boolean }[] = [
    {maj: 2, selected: true},
    {maj: 5, selected: false}
  ]
  $animate: (keys: { top: number; left: number; translateY: string; translateX: string; scale: number }, option?) => Promise<void>;

  get popDelays() {
    return (this.$root as any).delays.modal
  }

  async popup(options: {
    fromPos: { x: number, y: number },
    title: string,
    PlayerType: 0 | 1,
    majs?: { maj: number, selected: boolean }[]
  }) {
    this.title = options.title
    this.PlayerType = options.PlayerType
    this.majs = options.majs || this.majs

    await this.$animate({
      left: options.fromPos.x - 300 / 2 + 10,
      top: options.fromPos.y - 200 / 2 + 10,
      scale: 0,
      translateX: '-50%',
      translateY: '-50%'
    }, {duration: 0})

    await this.$animate({
      left: 600 / 2,
      top: 960 / 2,
      scale: 1,
      translateX: '-50%',
      translateY: '-50%'
    }, {duration: this.popDelays.in})
    await sleep(this.popDelays.delay)
    await this.$animate({
      left: options.fromPos.x - 300 / 2 + 10,
      top: options.fromPos.y - 200 / 2 + 10,
      scale: 0,
      translateX: '-50%',
      translateY: '-50%'
    }, {duration: this.popDelays.out});
    (this.$parent.$refs.majseq as any).pushMaj(options.PlayerType,
        this.majs.filter(_ => _.selected)[0].maj)
  }

}
