








import {Component, Prop, Vue} from "vue-property-decorator";
import Node from "@/components/Node.vue";

const playerColors = ['pink', 'cyan']
@Component
export default class Player extends Vue {
  $animate: (keys: Partial<CSSStyleDeclaration>, option?) => Promise<void>;

  get moveDelay() {
    return (this.$root as any).delays.move
  }

  @Prop({default: 0, type: Number}) PlayerType: 0 | 1
  static nodes: { left: string, top: string } = (Node as any).NodeInfo
      .map(v => ({left: `${v.x}px`, top: `${v.y}px`,}))
  position: { x, y }

  async moveTo(fromId: number, toId: number): Promise<void> {
    // this.$animate(Player.nodes[fromId - 1], {duration: 1})
    this.position = (Node as any).NodeInfo[toId - 1]
    await this.$animate(Player.nodes[toId - 1], {duration: this.moveDelay,})
  }

  style(part: number) {
    return {
      zIndex: this.PlayerType ^ part ? 4 : 3,
      borderLeft: part ? `none` : `${playerColors[this.PlayerType]} solid 10px`,
      borderRight: !part ? `none` : `${playerColors[this.PlayerType]} solid 10px`,
      boxShadow: `0 0 15px ${playerColors[this.PlayerType]}`
    }
  }

  mounted() {
    this.$animate(Player.nodes[6 - 1], {duration: 0})

  }


}
