










import {Component, Vue} from "vue-property-decorator";
import Stage from "@/components/Stage.vue";
import * as logs from "@/assets/logs";

@Component
export default class Panel extends Vue {
  pending = false;
  log = logs.log3
  round = 0
  processing = false

  get stage(): Stage | any {
    return this.$parent.$children[0]
  }

  get s_log() {
    return this.log.split('COIN').filter((_, v) => v > 0).map(_ => 'COIN' + _)
  }

  mounted() {

  }

  async start() {
    // this.pending = true
    this.processing = true;
    (this.$refs.log as HTMLTextAreaElement).focus()
    for (const r of this.s_log.filter((_, v) => v >= this.round)) {
      (this.$refs.log as HTMLTextAreaElement).setSelectionRange(
          this.s_log.filter((_, v) => v < this.round).join('').length,
          this.s_log.filter((_, v) => v <= this.round).join('').length,
      )
      await this.stage.analysisLog(r, this.round)
      this.round++
      if (!this.pending)
        break
    }
    this.processing = false;
  }

  reset() {
    this.pending = false
    this.round = 0;
    this.stage.reset()
  }
}
