








import {Component, Prop, Vue} from "vue-property-decorator";
//@ts-ignore
// import node from '../assets/node.svg'
import $ from 'velocity-animate/velocity.js'


declare type NodeTypeVal = { color: string }
@Component
export default class Node extends Vue {
  static NodeType = {
    Normal: {color: '#eee'}, ResourceAvailable: {color: '#ea7bea'},
    ResourcePossible: {color: '#33ee33'}, Lab: {color: "yellow"}
  }
  static NodeInfo = [[532, 82, 1], [111, 200, -1], [282, 201, 999], [339, 201, 3], [499, 219, -1], [377, 318, 999], [579, 230, 999], [670, 156, 999], [654, 263, 999], [786, 290, 2], [271, 427, 999], [402, 419, 999], [516, 417, 999], [582, 346, 4], [655, 419, 999], [830, 503, 4], [830, 347, -1], [65, 597, 999], [121, 532, 2], [219, 533, 999], [339, 533, 999], [474, 532, 5], [516, 530, -1], [654, 504, 999], [831, 624, 999], [66, 662, 3], [152, 661, 999], [217, 662, 999], [370, 663, -1], [444, 663, 999], [517, 622, 999], [654, 703, 6], [759, 702, 999], [219, 769, -1], [282, 766, 999], [343, 772, 999], [518, 769, 999], [583, 769, 6], [654, 765, 6], [758, 767, 999], [830, 739, 999], [67, 901, 999], [216, 902, 999], [342, 845, 2], [412, 846, 999], [516, 845, 999], [636, 847, 999], [741, 848, 999], [830, 846, -1], [65, 980, -1], [352, 959, -1], [518, 934, 999], [728, 987, 999], [864, 984, 2], [218, 1053, 999], [318, 1054, 999], [466, 1056, 999], [517, 1068, 3], [808, 1080, 999], [217, 1180, 1], [318, 1178, 999], [466, 1179, 999], [518, 1180, 999], [742, 1079, -1]]
      .map((v, idx) => ({x: v[0] / 1.5 - 20, y: v[1] / 1.4, interval: v[2]}))

  /*@Prop({
    default: () => Node.NodeType.Normal,
  })*/
  nodeType?: NodeTypeVal = Node.NodeType.Normal
  @Prop({default: 6, type: Number}) nodeId?: number
  style: { [key: string]: string } = {
    // backgroundImage: `url("${node}")`,
    left: Node.NodeInfo[this.nodeId].x + 'px',
    top: Node.NodeInfo[this.nodeId].y + 'px',
  }

  get position() {
    return Node.NodeInfo[this.nodeId]
  }

  get styleInner() {
    return this.nodeType == Node.NodeType.ResourceAvailable ? {} : {backgroundColor: this.nodeType?.color || Node.NodeType.Normal.color}
  }

  $animate: (keys: Partial<CSSStyleDeclaration>, option?, el?: Element) => Promise<void>;

  async spawn() {
    this.nodeType = Node.NodeType.ResourceAvailable
    const e = this.$refs.lighten as Element
    // await this.$animate({transform: '', opacity: '1'}, {duration: 0}, e)
    const delay = 70
    for (let i = 0; i < 3; i++) {
      await this.$animate({backgroundColor: Node.NodeType.ResourceAvailable.color}, {duration: delay}, this.$refs.inner as Element)
      await this.$animate({backgroundColor: Node.NodeType.ResourcePossible.color}, {duration: delay}, this.$refs.inner as Element)
    }
    await this.$animate({backgroundColor: Node.NodeType.ResourceAvailable.color}, {duration: delay}, this.$refs.inner as Element)

  }

  mounted() {
    this.nodeType = Node.NodeInfo[this.nodeId].interval < 0 ? Node.NodeType.Lab :
        Node.NodeInfo[this.nodeId].interval > 100 ? Node.NodeType.Normal :
            Node.NodeType.ResourcePossible
  }
}
