








import {Component, Vue} from "vue-property-decorator";
import Maj from "@/components/Maj.vue";

@Component({
  components: {Maj}
})
export default class MajSeq extends Vue {
  majs: number[][] = [[], []]
  floor = Math.floor

  pushMaj(player: 0 | 1, maj: number) {
    this.majs[player].push(maj * 10 + this.majs[player].length / 20)
    this.majs[player].sort()
  }
}
