






import {Component, Prop, Vue} from "vue-property-decorator";
import DigitRoll from '@huoyu/vue-digitroll'

const playerColors = ['pink', 'cyan']

@Component({components: {DigitRoll}})
export default class Counter extends Vue {
  @Prop({default: 0, type: Number}) value: number
  @Prop({default: 0, type: Number}) PlayerType: 0 | 1 | -1

  get style() {
    return {
      color: this.PlayerType == -1 ? 'white' : playerColors[this.PlayerType],
      left: this.PlayerType == 1 ? undefined : 0,
      right: this.PlayerType == 0 ? undefined : 0,
      ...(this.PlayerType == -1 ? {
        position: 'relative',
        margin: 'auto',
      } : {})
    }
  }

}
