




import {Component, Prop, Vue} from 'vue-property-decorator';
import Node from "@/components/Node.vue";

@Component
export default class Edge extends Vue {
  @Prop({default: 0}) fromId?: number;
  @Prop({default: 1}) toId?: number;
  nodes: { x: number, y: number, interval: number } = (Node as any).NodeInfo
  static EdgeInfo = [[1, 5], [1, 6], [1, 8], [2, 3], [3, 4], [3, 11], [4, 6], [4, 21], [6, 5], [6, 7], [6, 12], [6, 14], [5, 7], [5, 13], [5, 14], [7, 8], [7, 9], [7, 14], [8, 9], [8, 10], [9, 10], [9, 15], [10, 17], [11, 12], [11, 20], [11, 21], [12, 13], [12, 22], [13, 15], [13, 23], [14, 17], [15, 24], [16, 17], [16, 24], [16, 25], [17, 25], [18, 19], [18, 26], [19, 20], [20, 21], [20, 28], [21, 22], [21, 30], [22, 23], [22, 30], [23, 24], [23, 31], [24, 32], [25, 33], [25, 41], [26, 27], [26, 42], [27, 28], [28, 29], [28, 34], [29, 30], [29, 36], [30, 31], [30, 45], [31, 37], [32, 33], [32, 38], [32, 39], [33, 40], [33, 41], [34, 35], [34, 43], [35, 36], [36, 44], [37, 38], [37, 46], [38, 39], [39, 40], [39, 47], [40, 41], [41, 49], [42, 43], [42, 50], [43, 44], [43, 55], [44, 45], [44, 51], [45, 46], [46, 47], [46, 52], [47, 48], [47, 53], [48, 49], [48, 53], [48, 54], [48, 64], [49, 54], [50, 51], [50, 55], [51, 52], [51, 56], [52, 58], [53, 54], [53, 64], [54, 59], [55, 56], [55, 60], [56, 57], [56, 61], [57, 58], [57, 62], [58, 63], [58, 64], [59, 64], [60, 61], [61, 62], [62, 63]]
      .map(v => ({from: v[0], to: v[1]}))

  get style() {
    const fromVec = this.nodes[this.fromId || 0]
    const toVec = this.nodes[this.toId || 1]
    let length = Math.sqrt(Math.pow(toVec.y - fromVec.y, 2) + Math.pow(toVec.x - fromVec.x, 2))
    let center = {
      x: (toVec.x + fromVec.x) / 2,
      y: (toVec.y + fromVec.y) / 2
    }
    let rotate = Math.atan2((toVec.y - fromVec.y), (toVec.x - fromVec.x))
    const ret = {
      left: `${fromVec.x}px`,
      top: `${fromVec.y}px`,
      transform: `translate(10px,10px) rotateZ(${rotate}rad)`,
      transformOrigin: `0 0`,
      width: `${length}px`,
      // borderTop: `5px gray ${length > 30 ? 'dashed' : 'solid'}`

    }
    return ret
  }

  mouseEnter() {
    //@ts-ignore
    this.$parent.$children.filter(_ => _?.nodeId === this.fromId || _?.nodeId === this.toId).map(
        __ => __.$el.classList.add('hover'))
  }

  mouseLeave() {
    //@ts-ignore
    this.$parent.$children.filter(_ => _?.nodeId === this.fromId || _?.nodeId === this.toId).map(
        __ => __.$el.classList.remove('hover'))
  }

}
