














import {Component, Prop, Vue} from 'vue-property-decorator';
import Node from "@/components/Node.vue";
import Edge from "@/components/Edge.vue";
import Player from "@/components/Player.vue";
import Counter from "@/components/Counter.vue";
import Maj from "@/components/Maj.vue";
import Modal from "@/components/Modal.vue";
import MajSeq from "@/components/MajSeq.vue";
import Result from "@/components/Result.vue";

@Component({
  components: {Result, MajSeq, Modal, Maj, Counter, Player, Edge, Node}
})
export default class Stage extends Vue {
  nodes = (Node as any).NodeInfo
  edges = (Edge as any).EdgeInfo
  coin = {'0': 0, '1': 0}
  round = 0
  reloading = false
  showingResult = false

  reset() {
    this.reloading = true;
    this.$nextTick().then(
        () => {
          this.reloading = false;
        }
    )
    this.coin = {'0': 0, '1': 0}
    this.round = 0
    this.showingResult = false


  }

  p(i: number): Player | any {
    return this.$refs[`p${i}`][0] as Player
  }

  n(i: number): Node | any {
    return this.$refs[`node${i - 1}`][0] as Node
  }


  async analysisLog(log: string, roundAlready = 0) {
    this.round = roundAlready

    for (const line of log.split('\n')) {
      const name = line.split(' ')[0]
      const params = line.split(' ').filter((v, i) => i > 0).map(_ => Number.parseInt(_))
      console.log(line)
      switch (name) {
        case 'COIN':
          this.round++
          this.coin[0] = params[0]
          this.coin[1] = params[1]
          break;
        case 'MOVE':
        case 'OUTERRING':
        case 'WATERWAY':
          await this.p(params[0]).moveTo(params[1], params[2])
          break;
        case 'SPAWN':
          this.n(params[0]).spawn()
          break;
        case 'PICK':
          this.n(params[1]).nodeType = Node.NodeType.ResourcePossible;
          // this.coin[`${params[0]}`]++
          break;
        case 'LAB':
          (this.$refs.modal as any).majs = [
            {maj: params[0], selected: false},
            {maj: params[1], selected: false}
          ]
          break;
        case 'ADDMAJ':
          (this.$refs.modal as any).majs[params[1]].selected = name == 'ADDMAJ';
        case 'FLY':
          await (this.$refs.modal as any).popup({
            fromPos: this.p(params[0]).position,
            title: `从实验室选择`,
            PlayerType: params[0],
          } as any)
          break;
        case 'DELIVER':
          (this.$refs.modal as any).majs = [
            {maj: params[1], selected: true},
          ]
          await (this.$refs.modal as any).popup({
            fromPos: this.p(params[0]).position,
            title: `星际快递`,
            PlayerType: params[0],
          } as any)
          break;
        case'RED' :
          this.showingResult = true;
          (this.$refs.result as any).waiting[0] = params[0].toString().split('').map(_ => Number.parseInt(_));
          (this.$refs.result as any).score[0] = params[1]
          break;
        case'BLUE' :
          (this.$refs.result as any).waiting[1] = params[0].toString().split('').map(_ => Number.parseInt(_));
          (this.$refs.result as any).score[1] = params[1];
          (this.$refs.result as any).show()
          break;
        default :
          break;
      }
    }
  }


  get bg() {
    return {
      // background: `url("/img/background.png")`,
      // backgroundPosition: `${this.bgPos[0]}px ${this.bgPos[1]}px`
    }
  }
}
