

























import {Component, Vue} from "vue-property-decorator";
import Maj from "@/components/Maj.vue";

@Component({
  components: {Maj}
})
export default class Result extends Vue {
  waiting: number[][] = [[], []]
  score: number[] = [88, 2]
  $animate: (keys: Partial<CSSStyleDeclaration>, option?) => Promise<void>;

  show(waiting?: number[][], score?: number[]) {
    this.waiting = waiting || this.waiting;
    this.score = score || this.score;
    this.$forceUpdate()
    this.$animate({opacity: '1'}, {duration: 2000})
  }
}
